import {View, TouchableOpacity, ScrollView, Keyboard} from "react-native";
import {widthPercentageToDP as wp, heightPercentageToDP as hp} from 'react-native-responsive-screen';
import React, { useState, useEffect } from 'react';
import FitImage from 'react-native-fit-image';
import 'react-native-gesture-handler';
import axios from 'axios';

import {SlotMovieWrapper, SlotSectionTitle, SlotSectionValue, SlotButton, FlexRow, FlexColumn, TrailerWrapper, TrailerWrapperWrapper} from './components'
import MovieCard from "../components/movie-card/MovieCard";
import { CardWrapper } from "../components/movie-card/components";
import { WebView } from 'react-native-webview';


const SpinMovie = ({setSlotOpen}:any)=> {

    const [movie, setMovie] = useState(null);




    const findRandomId = async () =>{
      const randomPage = Math.floor(Math.random()*99+1);
      const randomIndex = Math.floor(Math.random()*19);
      const result =  await axios.get(`https://api.themoviedb.org/3/movie/popular?api_key=0e84972994557c7d52269649f653fb87&page=${randomPage}`);
        if(!result || !result.data || !result.data.results || !result.data.results.length){
          findRandomId();
    }else{
        const imdbId = await axios.get(`https://api.themoviedb.org/3/movie/${result.data.results[randomIndex].id}/external_ids?api_key=0e84972994557c7d52269649f653fb87`);
        const trailer = await axios.get(`https://api.themoviedb.org/3/movie/${result.data.results[randomIndex].id}/videos?api_key=0e84972994557c7d52269649f653fb87&language=en-US`)

        return {randomId: imdbId.data.imdb_id, trailer: trailer.data && trailer.data.results.length ? trailer.data.results[1].key : null}
    }
    }

    const spinHandler = async () =>{
      const {randomId, trailer } = await findRandomId();
      const result = await axios.get(`http://www.omdbapi.com/?i=${randomId}&apikey=976eb399&plot=full`);
      console.log(result.data.Poster)
      if(result && result.data ){
          if(result.data.Error || result.data.Poster==="N/A" || result.data.imdbRating==="N/A"){
              spinHandler()
            }else{
                setMovie({...result.data, trailer: trailer});
                return;
            }
      }else{
        spinHandler();  
    }
    }
    useEffect(()=>{
        if(!movie){
            spinHandler();
        }
    }, [])

    console.log(movie && movie.trailer)

    return (
        <SlotMovieWrapper>
        {movie &&
            <>
                <CardWrapper>
                    <FitImage source={{uri: movie.Poster}} />
                </CardWrapper>
                <SlotSectionTitle>Title</SlotSectionTitle>
                <SlotSectionValue>{movie.Title}</SlotSectionValue>
                <SlotSectionTitle>Genres</SlotSectionTitle>
                <SlotSectionValue>{movie.Genre}</SlotSectionValue>
                <SlotSectionTitle>Actors</SlotSectionTitle>
                <SlotSectionValue>{movie.Actors}</SlotSectionValue>
                <SlotSectionTitle>Duration</SlotSectionTitle>
                <SlotSectionValue>{movie.Runtime}</SlotSectionValue>
                <SlotSectionTitle>Date</SlotSectionTitle>
                <SlotSectionValue>{movie.Released}</SlotSectionValue>
                <SlotSectionTitle>IMDB</SlotSectionTitle>
                <SlotSectionValue>{movie.imdbRating}</SlotSectionValue>
                <SlotSectionTitle>Description</SlotSectionTitle>
                <SlotSectionValue>{movie.Plot}</SlotSectionValue>
                <TrailerWrapperWrapper>

            <TrailerWrapper>
                <WebView
                    style={{flex:1}}
                    javaScriptEnabled={true}
                    source={{uri: `https://www.youtube.com/embed/${movie.trailer}?rel=0&autoplay=0&showinfo=0&controls=0&modestbranding=1`}}
                    />
            </TrailerWrapper>
                    </TrailerWrapperWrapper>
                <FlexRow>
                    <SlotButton onTouchEnd={()=>{setMovie(null); spinHandler();}}>Again</SlotButton>
                    <SlotButton onTouchEnd={()=>setSlotOpen(false)}>Done</SlotButton>
                </FlexRow>
                </> ||
            <FlexColumn>

            <SlotSectionTitle>Loading...</SlotSectionTitle>
            <SlotButton onTouchEnd={()=>setSlotOpen(false)}>Cancel</SlotButton>
            </FlexColumn>
            }
        </SlotMovieWrapper> 

  );
}

export default SpinMovie